import React, {Component} from 'react'
import {
    Link
} from "react-router-dom";

import lock from '../assets/images/icons/lock.svg';
import email from '../assets/images/icons/email.svg';
import Submit from "../Components/Submit";


export default class ResetPassword extends Component {
    constructor(props) {
        super(props);
        this.state = {
            textSubmit: '–û—Ç–ø—Ä–∞–≤–∏—Ç—å ',
            email: '',
            errorEmail: false,
            loading: false,
            send: false
        };

        this.handleEmail = this.handleEmail.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    email = 'example@example.com'

    handleEmail(e) {
        this.setState({email: e.target.value})
    }

    handleSubmit(e) {
        e.preventDefault()

        if (this.state.email !== this.email) {
            this.setState({errorEmail: true})
        } else {
            this.setState({errorEmail: false, textSubmit: '–û—Ç–ø—Ä–∞–≤–∫–∞', loading: true})
            setTimeout(() => this.setState({send: true}), 3000)
        }
    }

    render() {
        if (this.state.send) {
            return (
                <div className="reset-password success">
                    <img src={email} alt="–í–æ—Å—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–∏–µ –ø–∞—Ä–æ–ª—è" className="reset-password__img"/>
                    <div className="reset-password__title">–ü–∏—Å—å–º–æ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω–æ</div>
                    <div className="reset-password__sub-title">–ù–∞ —É–∫–∞–∑–∞–Ω–Ω—ã–π –≤–∞–º–∏ e-mail –±—ã–ª–æ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω–æ –ø–∏—Å—å–º–æ –¥–ª—è —Å–º–µ–Ω—ã
                        –ø–∞—Ä–æ–ª—è
                    </div>
                    <Link to="/" className="btn btn__submit">–í–µ—Ä–Ω—É—Ç—å—Å—è –Ω–∞ –≥–ª–∞–≤–Ω—É—é</Link>
                </div>
            )
        }
        return (
            <div className="reset-password">
                <img src={lock} alt="–í–æ—Å—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–∏–µ –ø–∞—Ä–æ–ª—è" className="reset-password__img"/>
                <div className="reset-password__title">–í–æ—Å—Å—Ç–∞–Ω–æ–≤–∏—Ç—å –ø–∞—Ä–æ–ª—å</div>
                <div className="reset-password__sub-title">–í–≤–µ–¥–∏—Ç–µ e-mail, –Ω–∞ –∫–æ—Ç–æ—Ä—ã–π —Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–ª–∏—Å—å —Ä–∞–Ω–µ–µ</div>
                <form action="" onSubmit={this.handleSubmit}>
                    <input type="text" name="email" disabled={this.state.loading} onChange={this.handleEmail}
                           placeholder="ip@livedune.com"/>
                    <div className="reset-password__error">{this.state.errorEmail ? '–ù–µ –≤–µ—Ä–Ω—ã–π email' : ''}</div>
                    <Submit loading={this.state.loading} text={this.state.textSubmit}/>
                </form>
                <Link className="reset-password__cancel" to="/">–û—Ç–º–µ–Ω–∞</Link>
            </div>
        );
    }
}